/* CSS File */
.counter-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid var(--gray-100);
  background: white;
  box-shadow: 0 12px 16px -4px rgba(16, 24, 40, 0.08), 0 4px 6px -2px rgba(16, 24, 40, 0.03);
}

.counter-item {
  display: flex;
  align-items: center;
  color: var(--gray-800);
  justify-content: space-between;
  gap: 32px;
}

.counter-item p {
  margin: 0;
}

.counter-buttons-group {
  display: flex;
  align-items: center;
}

.counter-buttons-group p {
  width: 35px;
  text-align: center;
}

.main-text {
  font-size: 1rem !important;
  line-height: 1.25rem !important;
  padding-bottom: 4px !important;
  font-weight: 400;
}

.secondary-text {
  font-size: 12px;
  line-height: 16px;
}

.done-button {
  margin-top: 10px;
  width: 100%;
}
.popover-trigger {
  box-sizing: border-box;
  gap: 8px;
  background: white;
  border-radius: var(--radius, 8px);
  border: 1px solid var(--gray-300, #d0d5dd);
  display: inline-flex;
  align-items: center;
  padding: 2px 14px;
  width: fit-content;
  line-height: 1;
  color: var(--gray-700);
  cursor: pointer;
  min-width: 11rem;
  height: 3rem;
}
.popover-trigger[data-state='opened'],
.popover-trigger:active,
.popover-trigger:focus-visible {
  border-color: hsl(var(--brand-300, 206, 100%, 76%));
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.08), 0 0 0 4px hsla(var(--brand-100, 209, 100%, 91%), 0.2);
}
.popover-trigger p {
  margin: 0;
  padding: 0;
  font-size: 0.75rem;
}
.guests {
  padding-top: 6px !important;
  color: var(--gray-600);
  font-size: 0.875rem !important;
  font-weight: 400;
}
