:host {
  display: block;
}
.date-range-container {
  box-sizing: border-box;
  border-radius: 8px;
  border: 1px solid var(--gray-100);
  background: white;
  box-shadow: 0 12px 16px -4px rgba(16, 24, 40, 0.08), 0 4px 6px -2px rgba(16, 24, 40, 0.03);
}
.popover-trigger {
  box-sizing: border-box;
  gap: 8px;
  background: white;
  border-radius: var(--radius, 8px);
  border: 1px solid var(--gray-300, #d0d5dd);
  display: inline-flex;
  align-items: center;
  padding: 0px 14px;
  width: fit-content;
  min-width: 12.5rem;
  line-height: 1;
  color: var(--gray-700);
  cursor: pointer;
  /* height: 3rem; */
}
.popover-trigger[data-state='opened'],
.popover-trigger:active,
.popover-trigger:focus-visible {
  border-color: hsl(var(--brand-300, 206, 100%, 76%));
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.08), 0 0 0 4px hsla(var(--brand-100, 209, 100%, 91%), 0.2);
}
.popover-trigger p {
  margin: 0;
  padding: 0;
}
.date {
  padding-top: 6px !important;
  color: var(--gray-600);
  font-size: 0.875rem !important;
  font-weight: 400;
}
.label {
  position: absolute;
  transform-origin: left top;
  top: 50%;
  padding: 0px 14px;
  pointer-events: none;
  transition: all 0.3s ease-in-out;
  color: var(--gray-600, #475467);
  transform: translateY(-1.2rem);
  font-size: 0.75rem;
  line-height: 1rem;
}
.dates {
  min-block-size: 3rem;
  width: 100%;
  border-radius: var(--radius, 8px);
  padding-block-start: 1.5rem;
  outline: none;
  font-size: 14px;
  line-height: 1.25rem;
  padding-inline-end: 0.75rem;
}
