:host {
  display: block;
}
.card-container {
  border-radius: var(--radius, 8px);
  border: 1px solid var(--gray-300, #d0d5dd);
  transition: all 0.3s ease-in-out;
  width: 100%;
  position: relative;
  background: white;
  display: flex;
  align-items: center;
}
.card-container input {
  all: unset;
  width: 3rem;
  border-bottom: 1px solid var(--gray-600, #475467);
}
.card-number {
  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--gray-600, #475467);
  top: 50%;
  transform: translateY(-1.3rem);
  font-size: 0.75rem;
  line-height: 1rem;
}
.input-container {
  min-block-size: 3rem;
  width: 100%;
  border-radius: var(--radius, 8px);
  padding: 0px 14px;
  padding-block-start: 1rem;
  outline: none;
  font-size: 16px;
}
label:dir(rtl) {
  padding-right: 14px;
}
label:dir(ltr) {
  padding-left: 14px;
}
