textarea {
  width: 100%;
  min-height: 100px;
  margin: 0;
  padding: 10px 14px;
  border: 1px solid var(--gray-300, #d0d5dd);
  border-radius: var(--radius, 8px);
  font-size: 16px;
  transition: all 0.3s ease-in-out;
  outline: none;
}
textarea:focus {
  border-color: hsl(var(--brand-300, #84caff));
  box-shadow:
    0 1px 2px 0 rgba(0, 0, 0, 0.1),
    0 0 0 4px hsla(var(--brand-100, #d1e9ff), 0.1);
}
.error {
  border-color: var(--error-300, #fda29b);
}
textarea:disabled {
  background: var(--gray-50, #f9fafb);
  color: var(--gray-300, #d0d5dd);
  pointer-events: none;
}
label {
  position: absolute;
  top: 10px;
  z-index: 100;
  color: var(--gray-500, #667085);
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}
label:dir(rtl) {
  right: 14px;
}
label:dir(ltr) {
  left: 14px;
}
.container {
  position: relative;
}
.container:focus-within label,
.container.has-value label {
  background: white;
  transform: translateY(-1.4rem) scale(0.9);
}
