:host {
  display: block;
}
.date-range-container {
  box-sizing: border-box;
  border-radius: 8px;
  border: 1px solid var(--gray-100);
  background: white;
  box-shadow: 0 12px 16px -4px rgba(16, 24, 40, 0.08), 0 4px 6px -2px rgba(16, 24, 40, 0.03);
}
.popover-trigger {
  box-sizing: border-box;
  gap: 16px;
  background: white;
  border-radius: var(--radius, 8px);
  border: 1px solid var(--gray-300, #d0d5dd);
  display: inline-flex;
  align-items: center;
  padding: 0px 14px;
  min-width: 230px;
  line-height: 1;
  color: var(--gray-700);
  cursor: pointer;
  min-block-size: 3rem;

  /* height: 3rem; */
}
.popover-trigger[data-state='opened'],
.popover-trigger:active,
.popover-trigger:focus-visible {
  border-color: hsl(var(--brand-300, 206, 100%, 76%));
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.08), 0 0 0 4px hsla(var(--brand-100, 209, 100%, 91%), 0.2);
}
.trigger-label {
  margin: 0;
  padding: 0;
  font-size: 0.75rem;
  position: absolute;
  transform-origin: left top;
  padding-block-end: 0.5rem;
}
.date {
  padding-block-start: 1rem;
  color: var(--gray-600);
  font-weight: 400;
  font-size: 1rem;
  font-weight: 400;
  inline-size: 100%;
  line-height: 1.25rem;
}
